function linkLogo(){
	$('.how-block__logo').each(function(){
		var logo = $(this).attr('data-logo');

		$(this).css({
			'background-image': 'url(' + logo + ')',
			'background-repeat': 'no-repeat',
			'background-position': 'center'
		});
	})
}linkLogo()

$('.how-block').mouseenter(function(){
	var elem = $(this).find('.how-block__logo'),
	logoHover = $(elem).attr('data-logo-hover');

	$(elem).css({
		'background-image': 'url(' + logoHover + ')',
		'background-repeat': 'no-repeat',
		'background-position': 'center'
	});
})

$('.how-block').mouseleave(function(){
	linkLogo()
})