var
$headerHeight = $('.header-toggle').outerHeight(),
$hBillboard = $('.billboard').outerHeight(),
$scrolled,
$scrolledLeft;

function billboardIndent(){
	$('.billboard').css('top', 0);

	if($(window).scrollTop() > 725){
		$('.billboard').hide()
	}else if($(window).scrollTop() < 725){
		$('.billboard').show();
	}
}
billboardIndent()

function parallaxScroll(){
	$scrolled = $(window).scrollTop();
	$scrolledLeft = $(window).scrollLeft();

	if($(window).scrollTop() < 725){
		$('.header-toggle').css('top',(- ($scrolled*.5))+'px');
		$('.billboard').css('top',( - ($scrolled*.25))+'px');
		$('.billboard-block').css('top',( - ($scrolled*.75))+'px');
	}
}

function headerToggle(){
	if($(window).scrollTop() > 208){
		$('.header-toggle').hide();
	}else{
		$('.header-toggle').show();
	}
}
headerToggle();

function blockScroll(){
	$('.header-toggle').css('margin-left',-$scrolledLeft+'px');
	$('.billboard').css('margin-left',-$scrolledLeft+'px');
}

function blockWidth(){
	$('.header-toggle').css('width', $(document).width());
	$('.billboard').css('width', $(document).width());
}
blockWidth()

$(window).bind('resize',function(e){
	blockWidth()
})

$(window).bind('scroll',function(e){
	billboardIndent();
	parallaxScroll();
	blockScroll();
	headerToggle();
});